/* Cu hnh request cho tt c api - response cho tt c kt qu t api tr v */

//Cu hnh domain gi i
export const http = axios.create({
  baseURL: DOMAIN,
  timeout: 30000,
});
//Cu hnh request header
http.interceptors.request.use(
  (config: any) => {
    const token = getStoreJson(ACCESS_TOKEN);
    config.headers = {
      ...config.headers,
      ["Authorization"]: `Bearer ${token}`,
      ["TokenCybersoft"]: TOKEN_CYBERSOFT,
    };
    config.headers["Content-Type"] = "application/json";
    return config;
  },
  (error) => {
    Promise.reject(error);
  }
);
//Cu hnh kt qu tr v
http.interceptors.response.use(
  (response) => {
    console.log(response);
    return response;
  },
  (err) => {
    // const originalRequest = error.config;
    console.log(err.response.status);
    if (err.response.status === 400 || err.response.status === 404) {
      history.push("/");
      return Promise.reject(err);
    }
    if (err.response.status === 401 || err.response.status === 403) {
      alert("Token khng hp l ! Vui lng ng nhp li !");
      history.push("/login");
      return Promise.reject(err);
    }
  }
);
/**
 * status code
 * 400: Tham s gi ln khng hp l => kt qu khng tm c (Badrequest);
 * 404: Tham s gi ln hp l nhng khng tm thy => C th b xo ri (Not found) ...
 * 200: Thnh cng, OK
 * 201:  c to thnh cng => (Mnh  to ri sau  request tip th s tr 201) (Created)
 * 401: Khng c quyn truy cp vo api  (Unauthorize - C th do token khng hp l hoc b admin chn )
 * 403: Cha  quyn truy cp vo api  (Forbiden - token hp l tuy nhin token  cha  quyn truy cp vo api)
 * 500: Li xy ra ti server (Nguyn nhn c th frontend gi d liu khng hp l => backend trong qu trnh x l code gy ra li hoc do backend code b li => Error in server )
 */
